/* common */
button:hover {
    cursor: pointer;
}
img {
    width: 100px;
    height: 100px;
}
a {
    text-decoration: none;
    color:black;
}

.container {
    position: relative;
    width: 1200px;
    margin: auto;
}


/* HEADER */
header {
    height: 250px;
    background-color: bisque;
}

header > .container {
    position: relative;
    text-align: center;
    margin: auto;
    width: 1200px;
    height: inherit;
}

header > .container > .header_user {
    height: 20px;
}

header > .container > .header_user > ul {
    display: flex;
    justify-content: flex-end;
}

header > .container > .header_user > ul > li {
    padding-right: 15px;
}

header > .container > .header_user > ul > li:last-child {
    padding-right: 0;
}

header > .container > .header_logo {
    font-size: 32px;
    font-weight: 800;
    height: 150px;
    line-height: 150px;
}

header > .container > .header_category {
    height: 80px;
    line-height: 80px;
}

header > .container > .header_category > ul {
    display: flex;
    justify-content: center;
}

header > .container > .header_category > ul > li {
    font-size: 20px;
    font-weight: 600;
    padding-right: 60px;
}

/* MAIN */
.map_wrap,
.map_wrap * {
    margin: 0;
    padding: 0;
    font-family: 'Malgun Gothic', dotum, '돋움', sans-serif;
    font-size: 12px;
}

.map_wrap a,
.map_wrap a:hover,
.map_wrap a:active {
    color: #000;
    text-decoration: none;
}

.map_wrap {
    position: relative;
    width: 100%;
    height: 500px;
}

#menu_wrap {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 250px;
    margin: 10px 0 30px 10px;
    padding: 5px;
    overflow-y: auto;
    background: rgba(255, 255, 255, 0.7);
    z-index: 1;
    font-size: 12px;
    border-radius: 10px;
}

.bg_white {
    background: #fff;
}

#menu_wrap hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 2px solid #5F5F5F;
    margin: 3px 0;
}

#menu_wrap .option {
    text-align: center;
}

#menu_wrap .option p {
    margin: 10px 0;
}

#menu_wrap .option button {
    margin-left: 5px;
}

#placesList li {
    list-style: none;
}

#placesList .item {
    position: relative;
    border-bottom: 1px solid #888;
    overflow: hidden;
    cursor: pointer;
    min-height: 65px;
}

#placesList .item span {
    display: block;
    margin-top: 4px;
}

#placesList .item h5,
#placesList .item .info {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

#placesList .item .info {
    padding: 10px 0 10px 55px;
}

#placesList .info .gray {
    color: #8a8a8a;
}

#placesList .info .jibun {
    padding-left: 26px;
    background: url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_jibun.png) no-repeat;
}

#placesList .info .tel {
    color: #009900;
}

#placesList .item .markerbg {
    float: left;
    position: absolute;
    width: 36px;
    height: 37px;
    margin: 10px 0 0 10px;
    background: url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png) no-repeat;
}

#placesList .item .marker_1 {
    background-position: 0 -10px;
}

#placesList .item .marker_2 {
    background-position: 0 -56px;
}

#placesList .item .marker_3 {
    background-position: 0 -102px
}

#placesList .item .marker_4 {
    background-position: 0 -148px;
}

#placesList .item .marker_5 {
    background-position: 0 -194px;
}

#placesList .item .marker_6 {
    background-position: 0 -240px;
}

#placesList .item .marker_7 {
    background-position: 0 -286px;
}

#placesList .item .marker_8 {
    background-position: 0 -332px;
}

#placesList .item .marker_9 {
    background-position: 0 -378px;
}

#placesList .item .marker_10 {
    background-position: 0 -423px;
}

#placesList .item .marker_11 {
    background-position: 0 -470px;
}

#placesList .item .marker_12 {
    background-position: 0 -516px;
}

#placesList .item .marker_13 {
    background-position: 0 -562px;
}

#placesList .item .marker_14 {
    background-position: 0 -608px;
}

#placesList .item .marker_15 {
    background-position: 0 -654px;
}

#pagination {
    margin: 10px auto;
    text-align: center;
}

#pagination a {
    display: inline-block;
    margin-right: 10px;
}

#pagination .on {
    font-weight: bold;
    cursor: default;
    color: #777;
}


/* Footer */
.footer {
    background-color: #777;
}
.footer .inner{
    position: relative;
    width: 1350px;
    height: 220px;
    margin: 0 auto;
    padding: 50px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    font-size: 18px;
    font-weight: lighter;
    
}
.footer .inner .maker {
    font-weight: lighter;
    line-height: 30px;
}
.footer .inner .contact {
    text-align: center;
    font-weight: 600;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left:50%;
    transform: translate(-50%,-50%);
    
}
.footer .inner .contact img {
    width: 50px;
    border-radius: 100px;
    margin: 20px 10px 30px 0;
}
.footer .inner .contact li.inquiry {
    display: flex;
    justify-content: center; 
}

.footer .inner .contact li.inquiry .inquiry__title{
    color: #777;
    font-weight: lighter;
    font-size: 18px;
}
.footer .inner .contact li.inquiry .inquiry__inform {
    font-size: 16px;
    font-weight: lighter;
    margin: 10px 15px 20px 15px;
}
.footer .inner .contact .working {
    color: #777;
    font-weight: lighter;
    font-size: 14px;
}
.footer .inner .processing {
    text-align: end;
    line-height: 35px;
}
.footer .inner .processing  .processing__title {
    font-weight: 500;
}
